
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background-color: #fff;
}
.header .container {
  line-height: 145px;
}
.header .container .avatar {
  padding: 10px 0px 10px 0px;
}
.header .container .avatar img {
  position: relative;
  margin: auto;
}
.header .container .nav .menu {
  text-align: right;
}
.header .container .nav .menu li {
  line-height: 18px;
  display: inline-block;
  position: relative;
  padding: 10px 8px 10px 8px;
}
.header .container .nav .menu li a {
  text-transform: uppercase;
  color: #555;
  position: relative;
  letter-spacing: 1px;
  padding-bottom: 8px;
}
.header .container .nav .menu li a:hover {
  color: #84b2d2;
}
.header .container .nav .menu li a.nav-item::after {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #84b2d2;
  content: '';
  opacity: 0;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}
.header .container .nav .menu li a.nav-item:hover::after {
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, transform 0.3s;
  transition: opacity 0.3s, transform 0.3s, -webkit-transform 0.3s;
}


